/**
 * The following styles get applied inside the editor only.
 *
 * Replace them with your own styles or remove the file completely.
 */

@import "../../../theme-defaults.scss";

$EDITOR__column-border-color: $EDITOR-LIGHT-GRAY-COLOR;
$EDITOR__column-background-color: $EDITOR-WHITE-COLOR;

.wp-block[data-type="e25m/column"] {
  width: 100%;
  max-width: 100%;

  .wp-block {
    max-width: 100%;
  }

  &.d-flex {
    > .block-editor-inner-blocks {
      > .block-editor-block-list__layout {
        display: flex;
      }
    }
  }

  &.d-inline-flex {
    > .block-editor-inner-blocks {
      > .block-editor-block-list__layout {
        display: inline-flex;
      }
    }
  }

  &.is-selected {
    .block-editor-block-list {
      &__block {
        margin: 0;
      }
    }
  }

  &.disable {
    opacity: 0.4;
  }

  .block-editor-block-list {
    &__block {
      margin: 0;

      .block-list-appender {
        margin: 0;
      }
    }
  }
}

@media (min-width: 576px) {
  .wp-block[data-type="e25m/column"] {
    &.d-sm-flex {
      > .block-editor-inner-blocks {
        > .block-editor-block-list__layout {
          display: flex;
        }
      }
    }

    &.d-sm-inline-flex {
      > .block-editor-inner-blocks {
        > .block-editor-block-list__layout {
          display: inline-flex;
        }
      }
    }
  }
}

@media (min-width: 768px) {
  .wp-block[data-type="e25m/column"] {
    &.d-md-flex {
      > .block-editor-inner-blocks {
        > .block-editor-block-list__layout {
          display: flex;
        }
      }
    }

    &.d-md-inline-flex {
      > .block-editor-inner-blocks {
        > .block-editor-block-list__layout {
          display: inline-flex;
        }
      }
    }
  }
}

@media (min-width: 992px) {
  .wp-block[data-type="e25m/column"] {
    &.d-lg-flex {
      > .block-editor-inner-blocks {
        > .block-editor-block-list__layout {
          display: flex;
        }
      }
    }

    &.d-lg-inline-flex {
      > .block-editor-inner-blocks {
        > .block-editor-block-list__layout {
          display: inline-flex;
        }
      }
    }
  }
}

@media (min-width: 1200px) {
  .wp-block[data-type="e25m/column"] {
    &.d-xl-flex {
      > .block-editor-inner-blocks {
        > .block-editor-block-list__layout {
          display: flex;
        }
      }
    }

    &.d-xl-inline-flex {
      > .block-editor-inner-blocks {
        > .block-editor-block-list__layout {
          display: inline-flex;
        }
      }
    }
  }
}

@each $breakpoint in map-keys($grid-breakpoints) {
  @include media-breakpoint-up($breakpoint) {
    $infix: breakpoint-infix($breakpoint, $grid-breakpoints);

    .bs-column.flex#{$infix}-row > div > div {
      flex-direction: row !important;
    }
    .bs-column.flex#{$infix}-column > div > div {
      flex-direction: column !important;
    }
    .bs-column.flex#{$infix}-row-reverse > div > div {
      flex-direction: row-reverse !important;
    }
    .bs-column.flex#{$infix}-column-reverse > div > div {
      flex-direction: column-reverse !important;
    }

    .bs-column.flex#{$infix}-wrap > div > div {
      flex-wrap: wrap !important;
    }
    .bs-column.flex#{$infix}-nowrap > div > div {
      flex-wrap: nowrap !important;
    }
    .bs-column.flex#{$infix}-wrap-reverse > div > div {
      flex-wrap: wrap-reverse !important;
    }
    .bs-column.flex#{$infix}-fill > div > div {
      flex: 1 1 auto !important;
    }
    .bs-column.flex#{$infix}-grow-0 > div > div {
      flex-grow: 0 !important;
    }
    .bs-column.flex#{$infix}-grow-1 > div > div {
      flex-grow: 1 !important;
    }
    .bs-column.flex#{$infix}-shrink-0 > div > div {
      flex-shrink: 0 !important;
    }
    .bs-column.flex#{$infix}-shrink-1 > div > div {
      flex-shrink: 1 !important;
    }

    .bs-column.justify-content#{$infix}-start > div > div {
      justify-content: flex-start !important;
    }
    .bs-column.justify-content#{$infix}-end > div > div {
      justify-content: flex-end !important;
    }
    .bs-column.justify-content#{$infix}-center > div > div {
      justify-content: center !important;
    }
    .bs-column.justify-content#{$infix}-between > div > div {
      justify-content: space-between !important;
    }
    .bs-column.justify-content#{$infix}-around > div > div {
      justify-content: space-around !important;
    }

    .bs-column.align-items#{$infix}-start > div > div {
      align-items: flex-start !important;
    }
    .bs-column.align-items#{$infix}-end > div > div {
      align-items: flex-end !important;
    }
    .bs-column.align-items#{$infix}-center > div > div {
      align-items: center !important;
    }
    .bs-column.align-items#{$infix}-baseline > div > div {
      align-items: baseline !important;
    }
    .bs-column.align-items#{$infix}-stretch > div > div {
      align-items: stretch !important;
    }

    .bs-column.align-content#{$infix}-start > div > div {
      align-content: flex-start !important;
    }
    .bs-column.align-content#{$infix}-end > div > div {
      align-content: flex-end !important;
    }
    .bs-column.align-content#{$infix}-center > div > div {
      align-content: center !important;
    }
    .bs-column.align-content#{$infix}-between > div > div {
      align-content: space-between !important;
    }
    .bs-column.align-content#{$infix}-around > div > div {
      align-content: space-around !important;
    }
    .bs-column.align-content#{$infix}-stretch > div > div {
      align-content: stretch !important;
    }

    .bs-column.align-self#{$infix}-auto > div > div {
      align-self: auto !important;
    }
    .bs-column.align-self#{$infix}-start > div > div {
      align-self: flex-start !important;
    }
    .bs-column.align-self#{$infix}-end > div > div {
      align-self: flex-end !important;
    }
    .bs-column.align-self#{$infix}-center > div > div {
      align-self: center !important;
    }
    .bs-column.align-self#{$infix}-baseline > div > div {
      align-self: baseline !important;
    }
    .bs-column.align-self#{$infix}-stretch > div > div {
      align-self: stretch !important;
    }
  }
}

.berg-classic-theme {
  .wp-block[data-type="e25m/column"] {
    > .block-editor-inner-blocks {
      height: 100%;
      > .block-editor-block-list__layout {
        height: 100%;
      }
    }
    &.bs-column {
      margin: 0;
      padding: 0;
      border: 10px solid $EDITOR__column-border-color;
      background: $EDITOR__column-background-color;
      &:after,
      &:before {
        display: none;
      }
      .rich-text {
        padding: 5px;
      }
    }
  }
}
