// /**
//  * The following styles get applied inside the editor only.
//  *
//  * Replace them with your own styles or remove the file completely.
//  */

@import "../../../theme-defaults.scss";

$EDITOR__advanced-blurb-border-color: $EDITOR-PRIMARY-COLOR;
$EDITOR__advanced-blurb-button-background-color: #fff;
$EDITOR__advanced-blurb-button-border-color: #000;

.bs-advanced-blurb {
  @include blurb();
  &.disable {
    opacity: 0.4;
    pointer-events: none;
  }
}

//Control panel
.block-editor-block-list__block .block-editor-url-input input[type="text"],
.block-editor-url-input input[type="text"],
.components-popover .block-editor-url-input input[type="text"] {
  width: 200px !important;
  border: 1px solid black;
}

.components-panel__body {
  &.image-popup {
    .imageUploadWrapper {
      img {
        width: 50px;
        height: 50px;
        max-width: inherit;
        object-fit: cover;

        &[src=""] {
          display: none;
        }
      }
    }

    .image-popup-image,
    .image-popup-social {
      margin-bottom: 15px;

      .imageUploadWrapper {
        display: flex;
        align-items: center;
        margin-left: 15px;
      }
    }
  }

  &.video-popup {
    .videoUploadWrapper {
      video {
        width: 50px;
        height: 50px;
        object-fit: cover;
      }
    }

    .imageUploadWrapper {
      img {
        width: 50px;
        height: 50px;
        max-width: inherit;
        object-fit: cover;

        &[src=""] {
          display: none;
        }
      }
    }

    .video-popup-video,
    .video-popup-icon {
      margin-bottom: 15px;

      .videoUploadWrapper,
      .imageUploadWrapper {
        display: flex;
        align-items: center;
        margin-left: 15px;
      }
    }
  }

  .hover-image,
  .variation-image {
    margin-bottom: 15px;

    .imageUploadWrapper {
      display: flex;
      align-items: center;

      img {
        width: 50px;
        height: 50px;
        max-width: inherit;
        margin-left: 15px;
        object-fit: cover;

        &[src=""] {
          display: none;
        }
      }
    }
  }

  .bs-advance-blurb-popup-text {
    min-width: 200px;
    width: 100%;
    padding: 5px;
    border: 1px solid $THEME-PRIMARY-COLOR--royal-blue;
    color: $THEME-PRIMARY-COLOR--graish-blue;
  }
}

.berg-classic-theme {
  .wp-block[data-type="e25m/advanced-blurb"] {
    .bs-advanced-blurb {
      @include blurb-classic-view($EDITOR__advanced-blurb-border-color);
      &__title,
      &__subtitle,
      &__description,
      &__read-more {
        padding: 0 5px;
      }
    }
  }
}
