/**
 * The following styles get applied inside the editor only.
 *
 * Replace them with your own styles or remove the file completely.
 */
@import "../../../theme-defaults.scss";

.wp-block[data-type="e25m/embedded-forms"] {
  .button-wrapper {
    position: absolute;
    right: 0;
    top: 0;
    z-index: 1;
    button {
      &.reload {
        @include reload-button();
        margin: 5px;
      }
      &[disabled] {
        opacity: 0.2;
      }
    }
  }
}

.berg-classic-theme {
  .wp-block[data-type="e25m/embedded-forms"] {
    border: 5px solid $EDITOR-PRIMARY-COLOR;
    padding: 5px;
  }
}

.components-panel__body {
  .components-panel__row {
    &.download-panel {
      display: block;
      overflow: hidden;
    }
    &.video-popup-video {
      margin-bottom: 15px;
      .videoUploadWrapper {
        display: flex;
        align-items: center;
        margin-left: 15px;
        video {
          object-fit: cover;
        }
      }
    }
  }
}
