.wp-block[data-type='e25m/tab-slider-v2'] {
	.bs-tab-slider {
		> div:nth-of-type(1) {
			text-align: right;
			> button {
				@include action-button;
				margin: 5px 10px;
			}
		}
		> .bs-slider-tabs {
			@include slick-arrows();
			.slick-slide {
				> div {
					position: relative;
					min-height: 80px;
					margin: 10px 10px 0;
					padding: 25px 10px 10px;
					.button-wrapper {
						position: absolute;
						right: 0;
						top: 0;
					}
				}
				.slick-slide-wrapper {
					.block-editor-block-preview__container {
						height: auto !important;
						.block-editor-block-preview__content {
							transform: scale(1) !important;
							width: auto !important;
							position: static !important;
						}
					}
				}
			}
		}
		> .bs-slider-content {
			@include slick-arrows();
			.slick-slide {
				> div {
					min-height: 100px;
					margin: 5px 10px 10px;
					padding: 5px;
				}
				.slick-slide-wrapper {
					.block-editor-block-preview__container {
						height: auto !important;
						.block-editor-block-preview__content {
							transform: scale(1) !important;
							width: auto !important;
							position: static !important;
						}
					}
				}
			}
		}
		.button-wrapper {
			button {
				&.remove {
					@include delete-button();
				}
				&.next {
					@include next-button();
				}
				&.prev {
					@include prev-button();
				}
				&[disabled] {
					opacity: 0.2;
				}
			}
		}
		&.edit-mode {
			border: 1px solid $EDITOR-PRIMARY-COLOR;
			padding: 10px 5px;
			.wp-block[data-type="e25m/tab-slider-v2-tab"] {
				border: 1px dashed $EDITOR-DARK-GRAY-COLOR;
				padding: 10px 5px;
				position: relative;
				.blurred-content {
					position: absolute;
					top: 0;
					right: 0;
					bottom: 0;
					left: 0;
					z-index: 100;
					background-color: rgba(0, 0, 0, .2);
				}
				&:not(:last-child) {
					margin-bottom: 25px;
				}
				.wp-block[data-type="e25m/tab-slider-v2-tab-nav"] {
					margin-bottom: 25px;
				}
				.wp-block[data-type="e25m/tab-slider-v2-tab-nav"],
				.wp-block[data-type="e25m/tab-slider-v2-tab-content"] {
					border: 1px solid $EDITOR-PRIMARY-COLOR;
					padding: 10px 5px;
				}
				.collapsible {
					.collapsible-control {
						padding: 5px 10px;
						background-color: $EDITOR-PRIMARY-COLOR;
						color: white;
						position: relative;
						a {
							cursor: pointer;
							&:hover {
								text-decoration: underline;
							}
							&::after {
								position: absolute;
								top: 5px;
								right: 23px;
								width: 14px;
								height: 26px;
								content: "";
								background: url("data:image/png;base64,iVBORw0KGgoAAAANSUhEUgAAAA4AAAAaCAYAAACHD21cAAAAGXRFWHRTb2Z0d2FyZQBBZG9iZSBJbWFnZVJlYWR5ccllPAAAAEFJREFUeNpiZCAR/P//3wBIBZCsCYjfA/F/sjQBwXmyNAGxwKimkawJqrHhPwIokJ0GibZxVPOoZtqVctg0AwQYACrqrKTNESXQAAAAAElFTkSuQmCC")
									no-repeat 0 0 / 8px 13px;
								transform: rotate(90deg);
							}
						}
					}
					.collapsible-body {
						padding-top: 15px;
					}
					&.collapsed {
						.collapsible-control {
							a {
								&::after {
									top: 0;
									right: 10px;
									transform: rotate(270deg);
								}
							}
						}
						.collapsible-body {
							height: 0;
							padding-top: 0;
							transition: height .5s;
							overflow-y: hidden;
						}
					}
				}
			}
		}
		&:not(.edit-mode) {
      .block-list-appender {
        display: none;
      }
    }
	}
}

.berg-classic-theme {
	.wp-block[data-type='e25m/tab-slider-v2'] {
		border: 5px solid $EDITOR-PRIMARY-COLOR;
		padding: 5px;
		.bs-tab-slider {
			> .bs-slider-tabs {
				.slick-slide {
					> div {
						border: 5px solid $EDITOR-PRIMARY-COLOR;
						background: $EDITOR-LIGHT-GRAY-COLOR;
					}
					&.slick-current {
						div {
							background: $EDITOR-DARK-GRAY-COLOR;
						}
					}
				}
			}
			> .bs-slider-content {
				.slick-slide {
					> div {
						border: 5px solid $EDITOR-PRIMARY-COLOR;
					}
				}
			}
			&.disable {
				opacity: 0.4;
				pointer-events: none;
			}
		}
	}
}
