/**
 * The following styles get applied inside the editor only.
 *
 * Replace them with your own styles or remove the file completely.
 */

@import "../../../theme-defaults.scss";

.image-remove {
  color: $THEME-PRIMARY-COLOR--pink;
}

.popup-title {
  h2 {
    margin: 0;
  }
}

.popup-options {
  margin: 0 0 10px 0;
  color: $THEME-PRIMARY-COLOR--cool-grey;
}

.bs-pro-button__videopopupoptions {
  max-width: 100%;
}

.bs-pro-button__popup,
.bs-pro-button__videopopupoptions,
.bs-pro-button__download {
  embed {
    width: 100%;
  }

  > p {
    width: 100%;
    min-width: 200px;
    min-height: 100px;
    padding: 5px;
    border: 1px solid $THEME-PRIMARY-COLOR--royal-blue;
    color: $THEME-PRIMARY-COLOR--graish-blue;
  }

  .bs-pro-button-popup-text-title,
  .bs-pro-button-popup-text {
    width: 100%;
    min-width: 200px;
    padding: 5px;
    border: 1px solid $THEME-PRIMARY-COLOR--royal-blue;
    color: $THEME-PRIMARY-COLOR--graish-blue;
  }

  .bs-pro-button-popup-text {
    min-height: 150px;
  }

  button {
    border: 1px solid $THEME-PRIMARY-COLOR--catalina-blue;
  }
}

.bs-pro-button__videopopuplink {
  > div {
    width: 100%;
    padding: 5px;
    border: 1px solid;
  }
}

.bs-pro-button__videopopuphtml {
  margin-bottom: 5px;
  .input-validation-error {
    display: block;
    color: $form-feedback-invalid-color;
  }
}

.components-panel {
  &__row {
    .bs-pro-button__custom {
      width: 100%;

      .components-base-control {
        margin-bottom: 10px;
      }
    }
  }
}

.berg-classic-theme {
  .wp-block[data-type="e25m/pro-button"] {
    .bs-pro-button {
      padding: 0;
      border: 5px solid $EDITOR-PRIMARY-COLOR;
      a {
        display: inline-block;
        padding: 5px 12px;
        margin: 0;
        background: $EDITOR-WHITE-COLOR;
        color: $EDITOR-PRIMARY-COLOR;
        border-radius: 0;
        &::after,
        &::before {
          display: none;
        }
      }
      &.disable {
        opacity: 0.4;
        pointer-events: none;
      }
    }
  }
}
