/**
 * The following styles get applied inside the editor only.
 *
 * Replace them with your own styles or remove the file completely.
 */

@import "../../../theme-defaults.scss";

$EDITOR__blurb-border-color: $EDITOR-PRIMARY-COLOR;
$EDITOR__blurb-button-background-color: #fff;
$EDITOR__blurb-button-border-color: #000;

.bs-blurb {
  @include blurb();
  .hover-image,
  .variation-image {
    margin-bottom: 15px;

    .imageUploadWrapper {
      display: flex;
      align-items: center;

      img {
        width: 50px;
        height: 50px;
        max-width: inherit;
        margin-left: 15px;
        object-fit: cover;

        &[src=""] {
          display: none;
        }
      }
    }
  }
  &.disable {
    opacity: 0.4;
    pointer-events: none;
  }
}

.berg-classic-theme {
  .wp-block[data-type="e25m/blurb"] {
    .bs-blurb {
      @include blurb-classic-view($EDITOR__blurb-border-color);
      &__title,
      &__subtitle,
      &__description,
      &__read-more {
        padding: 0 5px;
      }
    }
  }
}
