/**
 * The following styles get applied inside the editor only.
 *
 * Replace them with your own styles or remove the file completely.
 */

@import "../../../theme-defaults.scss";

//Color values
$EDITOR__counter-border-color: $EDITOR-PRIMARY-COLOR;
$EDITOR__bs-counter__description--color: $THEME-PRIMARY-COLOR--charoal-grey;
$EDITOR__bs-counter__learn-more--color: $THEME-PRIMARY-COLOR--cool-grey;
$EDITOR__bs-counter__title--color: $THEME-PRIMARY-COLOR--black;

//Font Weights
$EDITOR__bs-counter__font-weight--medium: $THEME-FONT-WEIGHT-MEDIUM;
$EDITOR__bs-counter__font-weight--regular: $THEME-FONT-WEIGHT-REGULAR;

//Font Sizes
$EDITOR__bs-counter__font-size--small: 16px;
$EDITOR__bs-counter__font-size--large: 24px;

$EDITOR__bs-counter__description--font-size: $EDITOR__bs-counter__font-size--small;
$EDITOR__bs-counter__title--font-size: 22px;

//Title values
$EDITOR__bs-counter-title--line-height: 1.38;

//Description values
$DEFAULT__bs-counter-description--paragraph-limit: 3;

//Image Values
$DEFAULT__bs-counter__image--height: 24px;

.wp-block[data-type="e25m/counter"] {
  width: 100%;
  max-width: 100%;
}

.components-panel__body {
  .counter-prefix-preview {
    margin-bottom: 20px;
    img {
      margin-bottom: 10px;
      max-width: 110px;
    }
  }
  .components-base-control {
    width: 100%;
  }
  .components-range-control__root {
    .components-base-control {
      width: auto;
    }
  }
  .components-divider {
    border-color: $EDITOR__counter-border-color;
  }
  .editor-post-featured-image__container {
    width: 100%;
  }
}

.berg-classic-theme {
  .wp-block[data-type="e25m/counter"] {
    border: 5px solid $EDITOR__counter-border-color;
    padding: 5px;
    .bs-counter {
      &__wrapper {
        @include clearfix;
        line-height: 1;
        margin-bottom: 20px;
      }
      &__title {
        font-size: $EDITOR__bs-counter__title--font-size;
        font-weight: $EDITOR__bs-counter__font-weight--medium;
        color: $EDITOR__bs-counter__title--color;
        line-height: $EDITOR__bs-counter-title--line-height;
      }
      &__summary {
        font-weight: $EDITOR__bs-counter__font-weight--regular;
        font-size: $EDITOR__bs-counter__description--font-size;
        color: $EDITOR__bs-counter__description--color;
      }
      .bs-counter {
        &__prefix,
        &__postfix {
          float: left;
          margin: 0 5px;
          text-align: center;
          > p {
            float: left;
            margin-bottom: 0;
            font-size: $EDITOR__bs-counter__font-size--large;
            font-weight: $EDITOR__bs-counter__font-weight--medium;
          }
          img {
            float: left;
            width: auto;
            height: $DEFAULT__bs-counter__image--height;
            max-height: $DEFAULT__bs-counter__image--height;
          }
        }
        &__count,
        &__unit {
          float: left;
          text-align: center;
          span {
            font-weight: $EDITOR__bs-counter__font-weight--regular;
            font-size: $EDITOR__bs-counter__font-size--large;
            color: $EDITOR__bs-counter__learn-more--color;
          }
        }
      }
      &.disable {
        opacity: 0.4;
        pointer-events: none;
      }
    }
  }
}