/**
 * The following styles get applied inside the editor only.
 *
 * Replace them with your own styles or remove the file completely.
 */

@import "../../../theme-defaults.scss";

$EDITOR__row-border-color: $EDITOR-DARK-GRAY-COLOR;
$EDITOR__row-background-color: $EDITOR-DARK-GRAY-COLOR;

.wp-block[data-type="e25m/row"] {
  width: 100%;
  max-width: 100%;

  .bs-row {
    display: block;

    > .block-editor-inner-blocks {
      > .block-editor-block-list__layout {
        display: flex;
        flex-wrap: wrap;
        margin-right: 0;
        margin-left: 0;
      }
    }

    &.justify-content-start {
      > .block-editor-inner-blocks {
        > .block-editor-block-list__layout {
          justify-content: flex-start !important;
        }
      }
    }

    &.justify-content-end {
      > .block-editor-inner-blocks {
        > .block-editor-block-list__layout {
          justify-content: flex-end !important;
        }
      }
    }

    &.justify-content-center {
      > .block-editor-inner-blocks {
        > .block-editor-block-list__layout {
          justify-content: center !important;
        }
      }
    }

    &.justify-content-between {
      > .block-editor-inner-blocks {
        > .block-editor-block-list__layout {
          justify-content: space-between !important;
        }
      }
    }

    &.justify-content-around {
      > .block-editor-inner-blocks {
        > .block-editor-block-list__layout {
          justify-content: space-around !important;
        }
      }
    }

    &.disable {
      opacity: 0.4;
      pointer-events: none;
    }
  }

  &.is-selected {
    > .block-editor-block-list {
      &__block {
        margin: 0;
      }
    }
  }

  &.block-editor-block-list {
    &__block {
      margin: 0;

      > div {
        > .block-editor-inner-blocks {
          > .block-editor-block-list__layout {
            > .block-list-appender {
              margin-top: 0;
              margin-bottom: 0;
              margin-left: calc(50% - 16px);
            }
          }
        }
      }
    }
  }
}

@media (min-width: 576px) {
  .wp-block[data-type="e25m/row"] {
    .bs-row {
      &.flex-sm-row-reverse {
        > .block-editor-inner-blocks {
          > .block-editor-block-list__layout {
            flex-direction: row-reverse;
          }
        }
      }
    }
  }
}

@media (min-width: 768px) {
  .wp-block[data-type="e25m/row"] {
    .bs-row {
      &.flex-md-row-reverse {
        > .block-editor-inner-blocks {
          > .block-editor-block-list__layout {
            flex-direction: row-reverse;
          }
        }
      }
    }
  }
}
@media (min-width: 992px) {
  .wp-block[data-type="e25m/row"] {
    .bs-row {
      &.flex-lg-row-reverse {
        > .block-editor-inner-blocks {
          > .block-editor-block-list__layout {
            flex-direction: row-reverse;
          }
        }
      }
    }
  }
}

@media (min-width: 1200px) {
  .wp-block[data-type="e25m/row"] {
    .bs-row {
      &.flex-xl-row-reverse {
        > .block-editor-inner-blocks {
          > .block-editor-block-list__layout {
            flex-direction: row-reverse;
          }
        }
      }
    }
  }
}

.berg-classic-theme {
  .wp-block[data-type="e25m/row"] {
    .bs-row {
      margin: 0 0 10px;
      border: 10px solid $EDITOR__row-border-color;
      background: $EDITOR__row-background-color;
      &:after,
      &:before {
        display: none;
      }
    }
  }
}
