/**
 * The following styles get applied on the backend
 */
@import "../../../theme-defaults.scss";

$EDITOR-PRIMARY-DARK-COLOR: #0d9278;
$EDITOR-PRIMARY-LIGHT-COLOR: #4ebd9c;
$EDITOR-BROWN-COLOR: #555;
$EDITOR-BLOCK-APPENDER-COLOR: #1e1e1e;

[data-type="e25m/mobile-content"] {
  border: 2px solid $EDITOR-PRIMARY-COLOR;
  max-width: 100%;
  .components-button:hover,
  .components-button[aria-selected="true"],
  .components-button[aria-expanded="true"] {
    color: $THEME-PRIMARY-COLOR--black;
  }
  .components-tab-panel {
    &__tabs {
      margin: 0 15px;
    }
    &__tabs-item {
      .tooltip-icon {
        display: none;
      }
      &.tab-non-identical {
        &.bs-active-tab {
          .tooltip-icon {
            display: block;
            margin-left: 5px;
            width: 15px;
            height: 15px;
            border-radius: 50%;
            background-color: $EDITOR-PRIMARY-LIGHT-COLOR;
            font-size: 12px;
            color: $EDITOR-WHITE-COLOR;
          }
        }
      }
      &.bs-active-tab,
      &:focus:not(:disabled) {
        border-bottom: 3px solid $EDITOR-PRIMARY-COLOR;
        box-shadow: unset;
      }
      &.tab-disabled {
        opacity: 0.5;
        pointer-events: none;
      }
    }
  }
  .components-panel {
    &__body {
      border-bottom: none;
      border-top: 1px solid $EDITOR-PRIMARY-COLOR;
      padding: 0;
      .block-editor-button-block-appender {
        width: 24px;
        height: 24px;
        padding: 0;
        color: $EDITOR-WHITE-COLOR;
        background-color: $EDITOR-BLOCK-APPENDER-COLOR;
        border-radius: 2px;
        &:hover {
          background: var(--wp-admin-theme-color);
          box-shadow: inset 0 0 0 1px var(--wp-admin-theme-color);
        }
      }
    }
  }

  [data-type="e25m/desktop-content-wrapper"],
  [data-type="e25m/mobile-content-wrapper"] {
    display: none;
    margin-top: 0;
    max-width: 100%;
    > .block-editor-inner-blocks {
      border: 15px solid transparent;
    }
  }
  [data-type="e25m/desktop-content-wrapper"] {
    .wp-block {
      max-width: 100%;
    }
  }
  [data-type="e25m/mobile-content-wrapper"] {
    > .block-editor-inner-blocks {
      max-width: 992px;
      margin-left: auto;
      margin-right: auto;
    }
  }
  .mobile-content-tabs {
    &__button-wrapper {
      position: relative;
      .components-button {
        &:first-child:not(:last-child) {
          right: 110px;
        }
        &:not(:last-child) {
          right: 55px;
        }
        &.is-secondary {
          color: $EDITOR-PRIMARY-DARK-COLOR;
          &:active:not(:disabled),
          &:hover:not(:disabled) {
            color: $EDITOR-WHITE-COLOR;
            background: $EDITOR-PRIMARY-LIGHT-COLOR;
          }
        }
        &.is-secondary {
          box-shadow: inset 0 0 0 1px $EDITOR-PRIMARY-DARK-COLOR;
        }
        position: absolute;
        width: 50px;
        margin: 5px 15px;
        top: 0;
        right: 0;
        justify-content: center;
        &:hover {
          text-decoration: none;
        }
      }
    }
  }
  .tab-desktop {
    &.bs-active {
      [data-type="e25m/desktop-content-wrapper"] {
        display: block;
      }
    }
  }
  .tab-mobile {
    &.bs-active {
      [data-type="e25m/mobile-content-wrapper"] {
        display: block;
      }
    }
  }
}

// Confirmation modal
.mobile-content-modal {
  &__button-wrapper {
    margin-top: 20px;
    .components-button {
      &:not(:last-child) {
        margin-right: 5px;
      }
    }
  }
}

// Mobile content preview mode
.mobile-content-preview-mode {
  min-width: 100%;
  min-height: 100vh;
  margin: 2em auto 0;
  display: flex;
  flex-wrap: wrap;
  align-items: center;
  flex-direction: column;
  font: 1em sans-serif;
  box-sizing: border-box;
  .tabs {
    margin: 0 auto 2em;
    display: flex;
    text-align: center;
    border: 1px solid $EDITOR-PRIMARY-DARK-COLOR;
    border-radius: 2px;
    overflow: hidden;
    .tab {
      color: $EDITOR-PRIMARY-DARK-COLOR;
      background-color: $EDITOR-WHITE-COLOR;
      font-size: 25px;
      text-decoration: none;
      padding: 11px 9px;
      width: 100px;
      display: inline-block;
      transition: 0.1s;
      &:not(:last-of-type) {
        border-right: 1px solid $EDITOR-PRIMARY-DARK-COLOR;
      }
      &.active {
        color: $EDITOR-WHITE-COLOR;
        background: $EDITOR-PRIMARY-LIGHT-COLOR;
      }
    }
  }
  .ipad {
    padding: 23px 23px 23px 23px;
    margin-bottom: 2em;
    background: 0 0/740px no-repeat;
    height: 1048px;
    width: 694px;
    border-radius: 53px;
    position: relative;
    box-shadow: 2px 2px 16px $EDITOR-BLACK-COLOR;
    iframe {
      margin-top: 84px;
      margin-left: 18px;
      display: block;
      width: 659px;
      height: 877px;
      border-radius: 2px;
    }
  }

  .iphone {
    padding: 23px 23px 23px 25px;
    margin-bottom: 2em;
    background: -1px -1px/427px no-repeat;
    height: 817px;
    width: 377px;
    border-radius: 61px;
    position: relative;
    box-shadow: 2px 2px 16px $EDITOR-BLACK-COLOR;
    &:after {
      content: "";
      background: inherit;
      position: absolute;
      top: 0;
      left: 0;
      right: 0;
      bottom: 0;
      border-radius: inherit;
      clip: rect(0px, 321px, 55px, 107px);
    }
    iframe {
      display: block;
      width: 378px;
      height: 821px;
      border-radius: 38px;
    }
  }

  .desktop {
    width: calc(100% - 7em);
    border: 0.5em solid $EDITOR-BROWN-COLOR;
    border-radius: 0.25em;
    box-shadow: 2px 2px 16px $EDITOR-BLACK-COLOR;
    position: relative;
    iframe {
      width: 100%;
      height: calc(100vh - 7em);
    }
  }
}
