/**
 * The following styles get applied inside the editor only.
 *
 * Replace them with your own styles or remove the file completely.
 */

@import "../../../theme-defaults.scss";

$EDITOR__blurb-border-color: $EDITOR-PRIMARY-COLOR;
$EDITOR__blurb-button-background-color: #fff;
$EDITOR__blurb-button-border-color: #000;

.accordion-section {
  position: relative;
  .block-list-appender {
    position: relative;
    width: 50%;
    margin: 2px;
  }
  &.right-panel-floating {
    &::after {
      content: "";
      position: absolute;
      background: #e0e0e0;
      width: calc(50% - 23px);
      right: 0;
      top: 0;
      height: 100%;
      display: flex;
      z-index: 1;
    }
  }
  &.left-panel-floating {
    &::after {
      content: "";
      position: absolute;
      background: #e0e0e0;
      width: calc(50% - 23px);
      left: 0;
      top: 0;
      height: 100%;
      display: flex;
      z-index: 1;
    }
    .accordion__block {
      &__card-header,
      .wp-block[data-type="e25m/advance-accordion-top"] {
        margin-left: auto;
      }
    }
  }
  &.bottom-panel {
    .accordion__block__card-header {
      width: 100%;
    }
  }
}
.accordion__block {
  &__card {
    margin-bottom: 2px;
    &.bs-fade {
      opacity: 0.4;
      pointer-events: none;
    }
  }
  &__card-header {
    width: 50%;
    h5 {
      margin: 0;
    }
  }
  &__card-header-icon {
    margin-right: 10px;
    width: 50px;
    height: 50px;
    img {
      margin-right: 0;
      width: 100%;
      height: 100%;
      object-fit: scale-down;
    }
  }
  &__btn {
    font-size: 20px;
    border: none;
    cursor: pointer;
    font-family: "SailecRegular", sans-serif;
    padding: 15px 20px;
    background-color: #1abc9c;
    width: 100%;
    text-align: left;
    color: #fff;
    &:focus,
    &:active,
    &:hover {
      background-color: #10a587;
    }
  }
  &__desc {
    font-family: "SailecRegular", sans-serif;
    .card-body {
      font-size: 16px;
      text-align: left;
      padding: 15px 20px;
      border: none;
    }
  }
}

.fade {
  transition: opacity 0.15s linear;
}
@media (prefers-reduced-motion: reduce) {
  .fade {
    transition: none;
  }
}
.fade:not(.show):not(.expand-fixed) {
  opacity: 0;
}
.collapse:not(.show):not(.expand-fixed) {
  display: none;
}

.collapsing {
  position: relative;
  height: 0;
  overflow: hidden;
  transition: height 0.35s ease;
}
@media (prefers-reduced-motion: reduce) {
  .collapsing {
    transition: none;
  }
}

.aac-add-card,
.aac-add-top-block {
  flex-direction: row-reverse;
  padding: 0 20px !important;
  margin: 10px 0px;
  .dashicon {
    margin-left: 10px !important;
    margin-right: 0 !important;
    position: relative;
    top: 2px;
  }
}

.wp-block[data-type="e25m/advance-accordion-top"] {
  margin: 0;
  width: 50%;
}

.wp-block[data-type="e25m/advance-accordion-card"],
.wp-block[data-type="e25m/advance-accordion-top"] {
  margin-top: 0;
  margin-bottom: 0;
  .accordion__block__btn {
    display: flex;
    align-items: center;
    &[aria-expanded="true"],h1,h2,h3,h4,h5,h6,span,p {
      pointer-events: all;
    }
    span{
      flex: 1;
      margin-right: 25px;
      color: #000000;
    }
  }
  .card-body {
    border: none;
    &.left-panel-floating {
      .wp-block[data-type="e25m/advance-accordion-body-left"] {
        position: relative;
        left: 0;
        top: 0;
        z-index: 9;
        background: #fff;
      }
    }
    &.right-panel-floating {
      .wp-block[data-type="e25m/advance-accordion-body-left"] {
        position: relative;
        right: 0;
        top: 0;
        z-index: 9;
        background: #fff;
      }
      > .block-editor-inner-blocks {
        > .block-editor-block-list__layout {
          flex-direction: row-reverse;
        }
      }
    }
    > .block-editor-inner-blocks {
      > .block-editor-block-list__layout {
        display: flex;
        gap: 5em;
        margin-top: 0px;
        margin-bottom: 0px;
      }
    }
    .wp-block[data-type="e25m/advance-accordion-body-left"],
    .wp-block[data-type="e25m/advance-accordion-body-right"],
    .bs-advance-accordion-top {
      width: 100%;
      margin-top: 20px;
      transition: all 0.4s ease-in-out;
      > .accordion-left-side,
      > .accordion-right-side {
        position: relative;
        min-height: 50px;
        padding: 10px;
        span.side-info {
          display: block;
          position: absolute;
          top: -26px;
          left: -2px;
          background-color: #1abc9c;
          color: #fff;
          font-size: 11px;
          padding: 5px 10px;
        }
      }
    }
    &.bottom-panel {
      > .block-editor-inner-blocks {
        > .block-editor-block-list__layout {
          flex-direction: column-reverse;
        }
      }
    }
  }
}

// block appender
/* Floating panel side styles here */
.wp-block[data-type="e25m/advance-accordion"] {
  &.block-editor-block-list__block {
    .left-panel-floating {
      .block-list-appender {
        width: 100%;
      }
    }
    .accordion-section {
      .block-list-appender {
        margin-bottom: 2px;
      }
    }
  }
  border: 10px solid #bbb;
}

// Card header - Editor panel
.components-panel__body {
  .image-preview {
    &__row {
      position: relative;
      min-height: 100px;
    }
    &__img {
      img {
        width: 111px;
        object-fit: scale-down;
      }
    }
  }
}
