/**
 * The following styles get applied inside the editor only.
 *
 * Replace them with your own styles or remove the file completely.
 */
@import "../../../theme-defaults.scss";
@import "common";

$EDITOR__post-block-border-color: $EDITOR-PRIMARY-COLOR;

.wp-block[data-type="e25m/post-block"] {
  width: 100%;
  max-width: 100%;
  .button-wrapper {
    position: absolute;
    right: 0;
    top: 0;
    z-index: 1;
    button {
      &.reload {
        @include reload-button();
        margin: 5px;
      }
      &[disabled] {
        opacity: 0.2;
      }
    }
  }
}

.berg-classic-theme {
  .wp-block[data-type="e25m/post-block"] {
    @include image-classic-view();
    padding: 5px;
    border: 5px solid $EDITOR__post-block-border-color;
    .bs-posts {
      &.disable {
        opacity: 0.4;
        pointer-events: none;
      }
    }
  }
}

.multi-filter:not(.select2-hidden-accessible) {
  visibility: visible !important;
  max-height: none !important;
}

.components-panel {
  .link-control-sidebar {
    input[type="text"] {
      width: 100% !important;
    }
  }
  .editor-post-featured-image.media-upload,
  .editor-post-featured-image__actions,
  .editor-post-featured-image__actions {
    display: flex;
    justify-content: center;
  }

  .editor-post-featured-image.media-upload {
    height: unset;
    min-height: 100px;
    .editor-post-featured-image__preview {
      margin-bottom: 0;
    }
    .editor-post-featured-image__actions {
      position: absolute;
      width: 100%;
      gap: 8px;
      opacity: 0;
      transition: opacity 0.3s ease-out;
      .editor-post-featured-image__action {
        color: #000;
        min-width: 80px;
        height: 34px;
        padding: 9px 14px;
        border-radius: 2px;
        backdrop-filter: blur(16px) saturate(180%);
        background: rgb(255 255 255 / 80%);
        box-shadow: 0 6px 8px 0 rgba(0, 0, 0, 0.05);
        font-size: 12px;
        &:hover{
          color: var(--wp-admin-theme-color,#3858e9);
        }
      }
    }
    &:hover {
      .editor-post-featured-image__actions {
        opacity: 1;
      }
    }
  }
}
