/**
 * The following styles get applied inside the editor only.
 *
 * Replace them with your own styles or remove the file completely.
 */

@import "../../../theme-defaults.scss";

$EDITOR__card-border-color: $EDITOR-PRIMARY-COLOR;

.bs-card {
  @include card();
  .hover-image {
    margin-bottom: 15px;
    .imageUploadWrapper {
      display: flex;
      align-items: center;
      img {
        width: 50px;
        height: 50px;
        max-width: inherit;
        margin-left: 15px;
        object-fit: cover;
        &[src=""] {
          display: none;
        }
      }
    }
  }
}
.bs-card__container--flex {
  display: flex;
  .bs-card__container-right {
    flex: 1;
  }
  .bs-card__container-left {
    width: 30%;
    .bs-card__image {
      figure {
        margin-bottom: 0
      }
    }
  }
}

.berg-classic-theme {
  .wp-block[data-type="e25m/card"] {
    .bs-card {
      @include card-classic-view($EDITOR__card-border-color);
      &__title,
      &__subtitle,
      &__description,
      &__read-more {
        padding: 0 5px;
      }
    }
  }
}
