/**
 * The following styles get applied inside the editor only.
 *
 * Replace them with your own styles or remove the file completely.
 */

@import "../../../theme-defaults.scss";
@import "./grid-styles";

$EDITOR__section-border-color: $EDITOR-PRIMARY-COLOR;
$EDITOR__section-background-color: $EDITOR-PRIMARY-COLOR;
$EDITOR__section-media-image-background-color: #ddd;
$EDITOR__section-media-video-background-color: #000;
$EDITOR__section-media-play-icon-hover-color: #dc1212;

.logo-slider {
  border: 10px solid #91d1b3;
  background: #91d1b3;
}

.logo-slider-inner {
  .logo__item {
    .logo__item__inner {
      img {
        height: 130px;
        object-fit: scale-down;
      }
    }
  }
}

.logo-button-wrapper {
  display: grid;
  align-items: center;
  justify-content: center;
  button.components-button {
    margin: 24px 0px 15px;
    background: #212529;
    color: #fff;
    font-weight: bold;
    border: 1px solid #9edec0;
    padding: 0px 50px;
  }
}
