/**
 * The following styles get applied inside the editor only.
 *
 * Replace them with your own styles or remove the file completely.
 */

@import "../../../theme-defaults.scss";

$EDITOR__section-border-color: $EDITOR-PRIMARY-COLOR;
$EDITOR__section-background-color: $EDITOR-PRIMARY-COLOR;
$EDITOR__section-media-image-background-color: #ddd;
$EDITOR__section-media-video-background-color: #000;
$EDITOR__section-media-play-icon-hover-color: #dc1212;
$EDITOR__section-collapse-button-text-color: #ffffff;
$EDITOR__section-collapse-button-background-color: #1e1e1e;

.wp-block[data-type="e25m/section"] {
  width: 100%;
  max-width: 100%;
  .bs-section {
    min-height: 60px;
    $this: &;
    &.disable {
      opacity: 0.4;
      pointer-events: none;
    }
    &.collapsed {
      .block-editor-inner-blocks {
        .block-editor-block-list__block {
          display: none;
        }
      }
    }
    .bs-section__button-container {
      z-index: 1;
      position: absolute;
      display: flex;
      float: left;
      .bs-section__collapse-button,
      .bs-section__duplicate-button {
        display: flex;
        flex-direction: row;
        width: 24px;
        height: 24px;
        padding: 2px;
        margin: 8px 5px 0 0;
        border: none;
        border-radius: 2px;
        box-shadow: none;
        background-color: $EDITOR__section-collapse-button-background-color;
        color: $EDITOR__section-collapse-button-text-color;
      }
    }
  }
  &.is-selected {
    > .block-editor-block-list {
      &__block {
        margin: 0;
      }
    }
  }
  > .block-editor-block-list {
    &__block {
      margin: 0;
      > .block-list-appender {
        margin: 0;
      }
    }
  }
}

.components-panel {
  .media-upload {
    width: 100%;
    height: 200px;
    background-color: $EDITOR__section-media-image-background-color;
    position: relative;
    margin-bottom: 30px;
    transition: all 0.1s ease-out;
    &.image-upload {
      .image-thumb-wrapper {
        width: 100%;
        height: 100%;
        &:after {
          content: "";
          position: absolute;
          width: 100%;
          height: 100%;
          background-color: $EDITOR__section-media-video-background-color;
          opacity: 0.3;
          left: 0;
        }
        .image-thumb {
          width: 100%;
          height: 100%;
          object-fit: cover;
        }
      }
      .media-button {
        color: $EDITOR__section-media-image-background-color;
        &:hover {
          opacity: 0.7;
        }
      }
    }
    &.video-upload {
      svg {
        position: absolute;
        left: 0;
        right: 0;
        margin: 0 auto;
        width: 40px;
        height: 40px;
        top: 50px;
      }
      .video-file-name {
        text-align: center;
        position: absolute;
        left: 0;
        right: 0;
        bottom: 40px;
        text-align: center;
      }
      &:hover {
        opacity: 0.8;
        svg {
          path {
            color: $EDITOR__section-media-play-icon-hover-color;
          }
        }
      }
    }
    .media-button {
      position: absolute;
      top: 0;
      right: 0;
      z-index: 2;
    }

    &:focus {
      .media-button {
        outline: none;
      }
    }
  }
  .input-field-wrapper {
    margin-bottom: 15px;
    .components-input-control {
      label {
        font-size: 13px;
        font-weight: 400;
        text-transform: none;
      }
    }
  }
}

.berg-classic-theme {
  .wp-block[data-type="e25m/section"] {
    .bs-section {
      border: 10px solid $EDITOR__section-border-color;
      background: $EDITOR__section-background-color;
      &:after,
      &:before {
        display: none;
      }

      h1,
      h2,
      h3,
      h4,
      h5,
      h6,
      p,
      li {
        color: black !important;
      }

      .bs-section__image-container {
        z-index: 0;
      }
    }
  }
}
