/**
 * The following styles get applied inside the editor only.
 *
 * Replace them with your own styles or remove the file completely.
 */

@import "../../../theme-defaults.scss";
@import "./mixins";

.berg-classic-theme {
  .wp-block[data-type="e25m/slider-v2"] {
    border: 5px solid $EDITOR-PRIMARY-COLOR;
    padding: 5px;
    .bs-slider {
      .slick-slider {
        .slick-slide {
          > div {
            border: 5px solid $EDITOR-PRIMARY-COLOR;
          }
        }
      }
      &.disable {
        opacity: 0.4;
        pointer-events: none;
      }
    }
  }
}
.wp-block[data-type="e25m/slider-v2"] {
  [data-slides] {
    > div {
      > div {
        display: flex;
        flex-wrap: wrap;
      }
    }
  }
  .bs-slider {
    > div:nth-of-type(1) {
      display: flex;
      justify-content: flex-end;
      > button {
        @include action-button;
        margin: 5px 10px;
      }
    }
    .slick-slider {
      @include slick-arrows();
      .slick-slide {
        > div {
          min-height: 100px;
          margin: 5px 10px 10px;
          padding: 5px;
          @include slider-v2-delete-button();
        }
        .slick-slide-wrapper {
          .block-editor-block-preview__container {
            height: auto !important;
            .block-editor-block-preview__content {
              transform: scale(1) !important;
              width: auto !important;
              position: static !important;
            }
          }
        }
      }
    }
    .regular.slider {
      @include slider-v2-delete-button();
    }
    &.edit-mode {
      border: 1px solid $EDITOR-PRIMARY-COLOR;
      padding: 10px 5px;
      .wp-block[data-type="e25m/slider-v2-tab"] {
        border: 1px dashed $EDITOR-DARK-GRAY-COLOR;
        padding: 10px 5px;
        position: relative;
        width: 100%;
        .blurred-content {
          position: absolute;
          top: 0;
          right: 0;
          bottom: 0;
          left: 0;
          z-index: 100;
          background-color: rgba(0, 0, 0, 0.2);
        }
        &:not(:last-child) {
          margin-bottom: 25px;
        }
        .collapsable {
          .collapsable-control {
            padding: 5px 10px;
            background-color: $EDITOR-PRIMARY-COLOR;
            color: white;
            z-index: 101;
            a {
              position: relative;
              cursor: pointer;
              display: block;
              &::after {
                content: "";
                position: absolute;
                display: block;
                top: 5px;
                right: 13px;
                width: 14px;
                height: 26px;
                background: url("data:image/png;base64,iVBORw0KGgoAAAANSUhEUgAAAA4AAAAaCAYAAACHD21cAAAAGXRFWHRTb2Z0d2FyZQBBZG9iZSBJbWFnZVJlYWR5ccllPAAAAEFJREFUeNpiZCAR/P//3wBIBZCsCYjfA/F/sjQBwXmyNAGxwKimkawJqrHhPwIokJ0GibZxVPOoZtqVctg0AwQYACrqrKTNESXQAAAAAElFTkSuQmCC")
                  no-repeat 0 0 / 8px 13px;
                transform: rotate(90deg);
              }
            }
          }
          &.collapsed {
            .collapsable-control {
              a {
                &:after {
                  top: 0;
                  right: 0;
                  transform: rotate(270deg);
                }
              }
            }
          }
          &.collapsed > .collapsable-body {
            height: 0;
            transition: height 0.5s;
            overflow-y: hidden;
          }
        }
      }
    }
    &:not(.edit-mode) {
      .block-list-appender {
        display: none;
      }
    }
  }
}
