@import "../../../theme-defaults.scss";

$EDITOR__lottie-button-color: #1abc9c;

.wp-block[data-type="e25m/lottie-animator"] {
  border: 4px solid $EDITOR-PRIMARY-COLOR;
  svg {
    max-height: 180px;
    background-color: $THEME-PRIMARY-COLOR--solitude;
  }
  h3 {
    margin: 15px;
  }
  & .disable {
    opacity: 0.4;
    pointer-events: none;
  }
  .block-title {
    display: block;
    margin: 10px;
    width: fit-content;
    padding: 5px 10px;
    background-color: $EDITOR__lottie-button-color;
    font-size: 11px;
    color: $THEME-PRIMARY-COLOR--white;
  }
}

.components-panel {
  & .copy-settings {
    display: block;
    margin: 10px;
  }
  .components-repeater-control {
    &__row {
      display: flex;
      justify-content: center;
      align-items: center;
      margin-bottom: 10px;
      max-height: 35px;
    }
    &__column {
      margin-bottom: 0;
      &:last-of-type {
        width: 10%;
        margin-bottom: 0;
      }
      &:not(:first-of-type) {
        padding-left: 5px;
      }
    }
    &__icon {
      cursor: pointer;
    }
  }
}
