@import "../../../theme-defaults.scss";

$EDITOR__related-posts-border-color: $EDITOR-PRIMARY-COLOR;

.bs-related-posts {
  & .block-editor-block-list__layout {
    display: flex;
    flex-wrap: wrap;
    padding: 5px;
  }

  &__items {
    &.auto {
      .block-editor-inner-blocks {

        .button-wrapper,
        .block-list-appender {
          display: none; // To hide single post refresh button and block appender when the post rendering mode is 'auto'
        }
      }
    }

    .block-editor-inner-blocks {
      width: 100%;
    }

    .bs-column {
      .block-editor-inner-blocks {
        .block-list-appender {
          display: none;
        }

        .wp-block[data-type="e25m/single-post"] {
          width: 100%;
        }
      }
    }
  }

  &.disable {
    opacity: 0.4;
    pointer-events: none;
  }
}

.components-panel {
  &__help-text {
    opacity: 0.7;
  }
}

.berg-classic-theme {
  .wp-block[data-type="e25m/related-posts"] {
    border: 5px solid $EDITOR__related-posts-border-color;
  }
}

/* Related posts meta box styles*/
.brp_button_holder {
  padding: 10px 0;
  float: right;
}

.brp_mb_manage {
  small {
    display: block;
  }
}