@import "../../../theme-defaults.scss";

$bs-slider-accordion: ".bs-slider-accordion";

#{$bs-slider-accordion} {
  position: relative;
  &.disable {
    opacity: 0.4;
    pointer-events: none;
  }

  .block-list-appender {
    width: 100%;
    position: relative;
    text-align: center;
    #{$bs-slider-accordion}__add-card {
      flex-direction: row-reverse;
      margin: 10px 0px;
      padding: 0 20px;
      .dashicon {
        position: relative;
        top: 2px;
        margin-left: 10px;
        margin-right: 0;
      }
    }
  }

  & .wp-block[data-type="e25m/slider-accordion-card"] {
    margin-top: 0;
    margin-bottom: 0;
    .accordion__block {
      &__card {
        margin-bottom: 2px;
        &.bs-fade {
          opacity: 0.4;
          pointer-events: none;
        }
      }
      &__card-header {
        width: 100%;
        h5 {
          margin: 0;
        }
      }
      &__card-header-icon {
        margin-right: 10px;
        width: 100px;
        height: 100px;
        img {
          margin-right: 0;
          width: 100%;
          height: 100%;
          object-fit: contain;
        }
      }
      &__desc {
        font-family: "SailecRegular", sans-serif;
        .card-body {
          padding: 15px 20px;
          border: none;
          font-size: 16px;
          text-align: left;
          & > .block-editor-inner-blocks {
            & > .block-editor-block-list__layout {
              display: flex;
              flex-direction: column-reverse;
              gap: 1em;
              margin-top: 0px;
              margin-bottom: 0px;
            }
          }
          .wp-block[data-type="e25m/slider-accordion-body-left"],
          .wp-block[data-type="e25m/slider-accordion-body-right"] {
            width: 100%;
            margin-top: 20px;
            transition: all 0.4s ease-in-out;
            > .accordion-left-side,
            > .accordion-right-side {
              position: relative;
              padding: 10px;
              min-height: 50px;
              span.side-info {
                display: block;
                position: absolute;
                top: -26px;
                left: -2px;
                padding: 5px 10px;
                font-size: 11px;
                background-color: #1abc9c;
                color: #fff;
              }
            }
          }
        }
      }
      &__btn {
        display: flex;
        width: 100%;
        padding: 15px 20px;
        border: none;
        text-align: left;
        align-items: center;
        font-size: 20px;
        font-family: "SailecRegular", sans-serif;
        background-color: #1abc9c;
        color: #fff;
        cursor: pointer;
        &:focus,
        &:active,
        &:hover {
          background-color: #10a587;
        }
        &[aria-expanded="true"],h1,h2,h3,h4,h5,h6,p,span {
          pointer-events: all;
        }
        span{
          flex: 1;
          margin-right: 30px;
          color: #000000;
        }
      }
    }
  }
}

/* Floating panel side styles here */
.wp-block[data-type="e25m/slider-accordion"] {
  border: 10px solid #bbb;
}

// Card header - Editor panel
.components-panel__body {
  .image-preview {
    &__row {
      position: relative;
      min-height: 100px;
    }
    &__img {
      width: 100px;
      height: 100px;
      img {
        width: 100%;
        height: 100%;
        object-fit: scale-down;
      }
    }
  }
}
