/**
 * The following styles get applied inside the editor only.
 *
 * Replace them with your own styles or remove the file completely.
 */

@import "../../../theme-defaults.scss";

$EDITOR__section-border-color: $EDITOR-PRIMARY-COLOR;

.wp-block[data-type="e25m/div"] {
  width: 100%;
  max-width: 100%;
  .bs-div {
    position: relative;
    &.disable {
      opacity: 0.4;
      pointer-events: none;
    }
    .wp-block {
      max-width: 100%;
    }
    > .d-flex {
      > .block-editor-inner-blocks {
        > .block-editor-block-list__layout {
          display: flex;
        }
      }
    }
    > .d-inline-flex {
      > .block-editor-inner-blocks {
        > .block-editor-block-list__layout {
          display: inline-flex;
        }
      }
    }
    &__video {
      margin: auto;
      width: 100%;
      height: 100%;
      position: absolute;
      top: 0;
      left: 0;
      bottom: 0;
      right: 0;
      .video-poster,
      video {
        position: absolute;
        width: 100%;
        height: 100%;
        object-fit: cover;
      }
      .video-poster {
        img {
          width: 100%;
          height: 100%;
          object-fit: cover;
        }
      }
    }
    &__inner {
      position: relative;
    }
  }
  &.is-selected {
    > .block-editor-block-list {
      &__block {
        margin: 0;
      }
    }
  }
  > .block-editor-block-list {
    &__block {
      margin: 0;
      > .block-list-appender {
        margin: 0;
      }
    }
  }
}

@media (min-width: 576px) {
  .wp-block[data-type="e25m/div"] {
    .bs-div {
      > .d-sm-flex {
        > .block-editor-inner-blocks {
          > .block-editor-block-list__layout {
            display: flex;
          }
        }
      }
      > .d-sm-inline-flex {
        > .block-editor-inner-blocks {
          > .block-editor-block-list__layout {
            display: inline-flex;
          }
        }
      }
    }
  }
}

@media (min-width: 768px) {
  .wp-block[data-type="e25m/div"] {
    .bs-div {
      > .d-md-flex {
        > .block-editor-inner-blocks {
          > .block-editor-block-list__layout {
            display: flex;
          }
        }
      }
      > .d-md-inline-flex {
        > .block-editor-inner-blocks {
          > .block-editor-block-list__layout {
            display: inline-flex;
          }
        }
      }
    }
  }
}

@media (min-width: 992px) {
  .wp-block[data-type="e25m/div"] {
    .bs-div {
      > .d-lg-flex {
        > .block-editor-inner-blocks {
          > .block-editor-block-list__layout {
            display: flex;
          }
        }
      }
      > .d-lg-inline-flex {
        > .block-editor-inner-blocks {
          > .block-editor-block-list__layout {
            display: inline-flex;
          }
        }
      }
    }
  }
}

@media (min-width: 1200px) {
  .wp-block[data-type="e25m/div"] {
    .bs-div {
      > .d-xl-flex {
        > .block-editor-inner-blocks {
          > .block-editor-block-list__layout {
            display: flex;
          }
        }
      }
      > .d-xl-inline-flex {
        > .block-editor-inner-blocks {
          > .block-editor-block-list__layout {
            display: inline-flex;
          }
        }
      }
    }
  }
}

@each $breakpoint in map-keys($grid-breakpoints) {
  @include media-breakpoint-up($breakpoint) {
    $infix: breakpoint-infix($breakpoint, $grid-breakpoints);

    .bs-div > .flex#{$infix}-row > div > div {
      flex-direction: row !important;
    }
    .bs-div > .flex#{$infix}-column > div > div {
      flex-direction: column !important;
    }
    .bs-div > .flex#{$infix}-row-reverse > div > div {
      flex-direction: row-reverse !important;
    }
    .bs-div > .flex#{$infix}-column-reverse > div > div {
      flex-direction: column-reverse !important;
    }

    .bs-div > .flex#{$infix}-wrap > div > div {
      flex-wrap: wrap !important;
    }
    .bs-div > .flex#{$infix}-nowrap > div > div {
      flex-wrap: nowrap !important;
    }
    .bs-div > .flex#{$infix}-wrap-reverse > div > div {
      flex-wrap: wrap-reverse !important;
    }
    .bs-div > .flex#{$infix}-fill > div > div {
      flex: 1 1 auto !important;
    }
    .bs-div > .flex#{$infix}-grow-0 > div > div {
      flex-grow: 0 !important;
    }
    .bs-div > .flex#{$infix}-grow-1 > div > div {
      flex-grow: 1 !important;
    }
    .bs-div > .flex#{$infix}-shrink-0 > div > div {
      flex-shrink: 0 !important;
    }
    .bs-div > .flex#{$infix}-shrink-1 > div > div {
      flex-shrink: 1 !important;
    }

    .bs-div > .justify-content#{$infix}-start > div > div {
      justify-content: flex-start !important;
    }
    .bs-div > .justify-content#{$infix}-end > div > div {
      justify-content: flex-end !important;
    }
    .bs-div > .justify-content#{$infix}-center > div > div {
      justify-content: center !important;
    }
    .bs-div > .justify-content#{$infix}-between > div > div {
      justify-content: space-between !important;
    }
    .bs-div > .justify-content#{$infix}-around > div > div {
      justify-content: space-around !important;
    }

    .bs-div > .align-items#{$infix}-start > div > div {
      align-items: flex-start !important;
    }
    .bs-div > .align-items#{$infix}-end > div > div {
      align-items: flex-end !important;
    }
    .bs-div > .align-items#{$infix}-center > div > div {
      align-items: center !important;
    }
    .bs-div > .align-items#{$infix}-baseline > div > div {
      align-items: baseline !important;
    }
    .bs-div > .align-items#{$infix}-stretch > div > div {
      align-items: stretch !important;
    }

    .bs-div > .align-content#{$infix}-start > div > div {
      align-content: flex-start !important;
    }
    .bs-div > .align-content#{$infix}-end > div > div {
      align-content: flex-end !important;
    }
    .bs-div > .align-content#{$infix}-center > div > div {
      align-content: center !important;
    }
    .bs-div > .align-content#{$infix}-between > div > div {
      align-content: space-between !important;
    }
    .bs-div > .align-content#{$infix}-around > div > div {
      align-content: space-around !important;
    }
    .bs-div > .align-content#{$infix}-stretch > div > div {
      align-content: stretch !important;
    }

    .bs-div > .align-self#{$infix}-auto > div > div {
      align-self: auto !important;
    }
    .bs-div > .align-self#{$infix}-start > div > div {
      align-self: flex-start !important;
    }
    .bs-div > .align-self#{$infix}-end > div > div {
      align-self: flex-end !important;
    }
    .bs-div > .align-self#{$infix}-center > div > div {
      align-self: center !important;
    }
    .bs-div > .align-self#{$infix}-baseline > div > div {
      align-self: baseline !important;
    }
    .bs-div > .align-self#{$infix}-stretch > div > div {
      align-self: stretch !important;
    }
  }
}

.berg-classic-theme {
  .wp-block[data-type="e25m/div"] {
    .bs-div {
      padding: 5px;
      border: 5px solid $EDITOR__section-border-color;
      background: $EDITOR-WHITE-COLOR;
      &:after,
      &:before {
        display: none;
      }
    }
  }
}
